        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!-- ÂèëÂ∏ÉÊñáÁ´†-ÂØπËØùÊ°Ü -->
    <el-dialog
      title="ÂèëË°®"
      :visible.sync="pubdialogVisible"
      fullscreen
      :before-close="handleClose"
      @closed="dialogCloseFn"
    >
      <el-form
        :model="pubForm"
        :rules="pubFormRules"
        ref="pubFormRef"
        label-width="100px"
      >
        <el-form-item label="ÊñáÁ´†Ê†áÈ¢ò" prop="title">
          <el-input v-model="pubForm.title" placeholder="ËØ∑ËæìÂÖ•Ê†áÈ¢ò"></el-input>
        </el-form-item>
        <el-form-item label="ÊñáÁ´†ÂàÜÁ±ª" prop="cate_id">
          <el-select
            v-model="pubForm.cate_id"
            placeholder="ËØ∑ÈÄâÊã©ÂàÜÁ±ª"
            style="width: 100%"
          >
            <!-- ÊñáÁ´†ÂàÜÁ±ªÈÄâÊã© - ÂèëÂ∏ÉÊñáÁ´†Âå∫Âüü -->
            <!-- labelÊòØÈ°µÈù¢Â±ïÁ§∫ÁöÑÂÄºÔºåvalueÁöÑüö©ÂÄºÊòØÁªôjsË∞ÉÁî®ÂæÖ‰ΩøÁî®ÁöÑÊï∞ÊçÆÔºàpubForm.cata_idÔºâ -->
            <el-option
              :label="item.cate_name"
              :value="item.id"
              v-for="item in cateList"
              :key="item.id"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <!-- ÂØåÊñáÊú¨ÁºñËæëÂô® -->
        <el-form-item label="ÊñáÁ´†ÂÜÖÂÆπ" prop="content">
          <quill-editor
            v-model="pubForm.content"
            @change="contentChangeFn"
          ></quill-editor>
        </el-form-item>
        <!-- ÊñáÁ´†Â∞ÅÈù¢ -->
        <el-form-item label="ÊñáÁ´†Â∞ÅÈù¢" prop="cover_img">
          <!-- Áî®Êù•ÊòæÁ§∫Â∞ÅÈù¢ÁöÑÂõæÁâá -->
          <img
            src="../../assets/images/cover.jpg"
            alt=""
            class="cover-img"
            ref="imgRef"
          />
          <br />
          <!-- Êñá‰ª∂ÈÄâÊã©Ê°ÜÔºåÈªòËÆ§Ë¢´ÈöêËóè -->
          <input
            type="file"
            style="display: none"
            accept="image/*"
            ref="iptFileRef"
            @change="onCoverChangeFn"
          />
          <!-- ÈÄâÊã©Â∞ÅÈù¢ÁöÑÊåâÈíÆ -->
          <el-button type="text" @click="chooseImgFn">+ ÈÄâÊã©Â∞ÅÈù¢</el-button>
        </el-form-item>
        <!-- ÂèëÂ∏É‰∏éÂ≠ò‰∏∫ËçâÁ®øÊåâÈíÆ -->
        <el-form-item>
          <el-button type="primary" @click="pubArticleFn('Â∑≤ÂèëÂ∏É')"
            >ÂèëÂ∏É</el-button
          >
          <el-button type="info" @click="pubArticleFn('ËçâÁ®ø')"
            >Â≠ò‰∏∫ËçâÁ®ø</el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>

    <!-- Êü•ÁúãÊñáÁ´†ËØ¶ÊÉÖ-ÂØπËØùÊ°Ü -->
    <el-dialog title="ÊñáÁ´†È¢ÑËßà" :visible.sync="detailVisible" width="80%">
      <h1 class="title">{{ artDetail.title }}</h1>

      <div class="info">
        <span>‰ΩúËÄÖÔºö{{ artDetail.nickname || artDetail.username }}</span>
        <span>ÂèëÂ∏ÉÊó∂Èó¥Ôºö{{ $formatDate(artDetail.pub_date) }}</span>
        <span>ÊâÄÂ±ûÂàÜÁ±ªÔºö{{ artDetail.cate_name }}</span>
        <span>Áä∂ÊÄÅÔºö{{ artDetail.state }}</span>
      </div>

      <!-- ÂàÜÂâ≤Á∫ø -->
      <el-divider></el-divider>

      <!-- ÊñáÁ´†ÁöÑÂ∞ÅÈù¢ -->
      <!-- ËøΩÂä†ÂõæÁâáÂü∫Âú∞ÂùÄ -->
      <img :src="baseURL + artDetail.cover_img" alt="" />

      <!-- ÊñáÁ´†ÁöÑËØ¶ÊÉÖ -->
      <!-- Ê≥®ÊÑè‚ú®ÔºöËøôÈáåÁöÑÂÜÖÂÆπË¶ÅÁî®v-htmlËøõË°åËß£Êûê -->
      <div v-html="artDetail.content" class="detail-box"></div>
    </el-dialog>
  </div>
</template>

<script>
import {
  getArtCateListAPI,
  uploadArticleAPI,
  getArticleListAPI,
  getArticleDetailFn,
  delArticleAPI
} from '@/api/index'
// Ê≥®ÊÑè‚ú®Ôºöjs‰∏≠ÂºïÂÖ•ÂõæÁâáË¶ÅÊâÄ‰ª•import
import defaultImg from '@/assets/images/cover.jpg'
// ÂºïÂÖ•ÂõæÁâáÂü∫Âú∞ÂùÄ
import { baseURL } from '@/utils/axios/axios-instance'

export default {
  name: 'articleList',
  data() {
    return {
      baseURL: baseURL,
      // ÊñáÁ´†ÂàÜÁ±ªÊï∞ÊçÆ
      cateList: [],
      // articleListÁªÑ‰ª∂ÁöÑÊü•ËØ¢ÂèÇÊï∞ÂØπË±°
      q: {
        pagenum: 1, // ÈªòËÆ§ÊòæÁ§∫Á¨¨‰∏ÄÈ°µÊï∞ÊçÆ
        pagesize: 5, // ÈªòËÆ§ÂΩìÂâçÈ°µÊòæÁ§∫Âá†Êù°Êï∞ÊçÆ
        cate_id: '',
        state: ''
      },
      // ÊñáÁ´†ÁöÑÂàóË°®Êï∞ÊçÆ
      artList: [],
      // ÊÄªÊï∞ÊçÆÊù°Êï∞
      total: 0,
      pubdialogVisible: false,
      detailVisible: false,
      // ÊñáÁ´†ËØ¶ÊÉÖÊï∞ÊçÆ
      artDetail: {},
      // ÂèëÂ∏ÉÊñáÁ´†ÂØπËØùÊ°ÜË°®ÂçïÊï∞ÊçÆÂØπË±°
      pubForm: {
        title: '',
        cate_id: '',
        content: '',
        cover_img: null, // Áî®Êà∑ÈÄâÊã©ÁöÑÂ∞ÅÈù¢ÂõæÁâáÔºànull Ë°®Á§∫Ê≤°ÊúâÈÄâÊã©‰ªª‰ΩïÂ∞ÅÈù¢Ôºâ
        state: '' // ÊñáÁ´†ÁöÑÂèëÂ∏ÉÁä∂ÊÄÅÔºåÂèØÈÄâÂÄºÊúâ‰∏§‰∏™ÔºöËçâÁ®ø„ÄÅÂ∑≤ÂèëÂ∏É
      },
      // ÂèëÂ∏ÉÊñáÁ´†ÂØπËØùÊ°ÜË°®ÂçïÈ™åËØÅËßÑÂàôÂØπË±°
      pubFormRules: {
        // Ë°®ÂçïÁöÑÈ™åËØÅËßÑÂàôÂØπË±°
        title: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÊñáÁ´†Ê†áÈ¢ò', trigger: 'blur' },
          {
            min: 1,
            max: 30,
            message: 'ÊñáÁ´†Ê†áÈ¢òÁöÑÈïøÂ∫¶‰∏∫1-30‰∏™Â≠óÁ¨¶',
            trigger: 'blur'
          }
        ],
        cate_id: [
          // ‰ΩøÁî®change‰∫ã‰ª∂‚ú®ÂØπ‰∏ãÊãâËèúÂçïËøõË°åÊ†°È™å
          { required: true, message: 'ËØ∑ÈÄâÊã©ÊñáÁ´†Ê†áÈ¢ò', trigger: 'change' }
        ],
        content: [
          // Áî±‰∫équil-editorÊòØÂºïÂÖ•ÁöÑÔºå‰∏ç‰ºöËá™Âä®Ëµ∞Ê†°È™å
          // Ëß£ÂÜ≥ÂäûÊ≥ïÔºöÁî®ÂÆÉËá™Â∏¶Ëß¶ÂèëÂáΩÊï∞Èó¥Êé•ËøõË°åÊ†°È™å
          { required: true, message: 'ËæìÂÖ•ÊñáÁ´†ÂÜÖÂÆπ', trigger: 'blur' }
        ],
        cover_img: [
          // ËøôÈáåÁöÑÂõæÁâáÊ†°È™åÂíåÂâçÈù¢ÁöÑÂØåÊñáÊú¨Âô®Ê†°È™å‰∏ÄÊ†∑ÁöÑËß£ÂÜ≥ÊñπÊ≥ïÔºàÂçïÁã¨Ê†°È™åÔºâ
          { required: true, message: 'ËØ∑ÈÄâÊã©Â∞ÅÈù¢', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    // ËøõÂÖ•È°µÈù¢Êó∂Â∞± ËØ∑Ê±ÇÂàÜÁ±ªÊï∞ÊçÆ„ÄÅÂàóË°®Êï∞ÊçÆ
    this.getCateListFn()
    this.getArtListFn()
  },
  methods: {
    // Ëé∑ÂèñÊñáÁ´†ÂàÜÁ±ª
    async getCateListFn() {
      const { data: res } = await getArtCateListAPI()
      // console.log(res)
      this.cateList = res.data
    },
    // Ëé∑ÂèñÊñáÁ´†ÂàóË°®
    async getArtListFn() {
      const { data: res } = await getArticleListAPI(this.q)

      // console.log(res)
      if (res.code !== 0) return this.$message.error('Ëé∑ÂèñÊñáÁ´†ÂàóË°®Â§±Ë¥•!')
      this.artList = res.data
      this.total = res.total
    },

    // ÂèëË°®ÊñáÁ´†-Ëß¶ÂèëÊåâÈíÆ
    // ‰ΩøÁî®dialogÂØπËØùÊ°Üü§îÔºàÂà†Èô§‰∫ÜÁ°ÆËÆ§„ÄÅÂèñÊ∂àÊåâÈíÆÔºâ
    showPubDialogFn() {
      this.pubdialogVisible = true
    },
    // ÂèëË°®ÊñáÁ´†-ÂØπËØùÊ°ÜÂÖ≥Èó≠ÂâçÁöÑÂõûË∞Éüö©
    // Ëøô‰∏™ÊñπÊ≥ïÂ∞Ü‰ª£ÊõøÂéüÂÖàdialogÂØπËØùÊ°ÜÁöÑÁ°ÆËÆ§„ÄÅÂèñÊ∂àÊåâÈíÆ
    handleClose(done) {
      // ‰ΩøÁî®element-uiÁöÑMessageBoxÂºπÊ°ÜÔºàÂâçÈù¢ÈÄÄÂá∫ÂäüËÉΩ‰πüÁî®ËøáÔºâ
      // ËøôÈáå‰ªéÂºÇÊ≠•ÁöÑËßíÂ∫¶ÂàÜÊûê
      // const confirmResult = await this.$confirm(‚Ä¶‚Ä¶)
      this.$confirm('Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•Êñá‰ª∂, ÊòØÂê¶ÁªßÁª≠?', 'ÊèêÁ§∫', {
        confirmButtonText: 'Á°ÆÂÆö',
        cancelButtonText: 'ÂèñÊ∂à',
        type: 'warning'
      })
        // Ëã•ÈÄâÊã©Á°ÆÂÆöÁöÑÊìç‰Ωú
        .then(() => {
          // ÂÖ≥Èó≠ÂØπËØùÊ°Ü
          done()
        })
        // Ëã•ÈÄâÊã©ÂèñÊ∂àÁöÑÊìç‰Ωú
        .catch(() => {
          this.$message.success('Â∑≤ÂèñÊ∂àÔºÅ')
        })
    },
    // Èó¥Êé•‰ª£ÊõøÊñá‰ª∂ÈÄâÊã©Ê°ÜÁöÑÁÇπÂáª‰∫ã‰ª∂
    chooseImgFn() {
      this.$refs.iptFileRef.click()
    },
    // Â∞ÅÈù¢ÈÄâÊã©ÊîπÂèòÁöÑ‰∫ã‰ª∂ÔºàÂõæÁâáÈ¢ÑËßàÔºöËøôÈáåÈááÁî®Á¨¨‰∫åÁßçÊñπÂºè „ÄêÊ≥®ÊÑèÊØîËæÉ‰∏éÂâçÈù¢ÁöÑÂõæÁâáÈ¢ÑËßàÊâÄ‰ª•ÁöÑÂå∫Âà´„ÄëÔºâ
    onCoverChangeFn(e) {
      // Ëé∑ÂèñÁî®Êà∑ÈÄâÊã©ÁöÑÊñá‰ª∂ÂàóË°®
      const files = e.target.files
      if (files.length === 0) {
        // Áî®Êà∑Ê≤°ÊúâÈÄâÊã©Â∞ÅÈù¢
        this.pubForm.cover_img = null

        this.$refs.imgRef.setAttribute('src', defaultImg)
      } else {
        // Áî®Êà∑ÈÄâÊã©‰∫ÜÂ∞ÅÈù¢
        this.pubForm.cover_img = files[0]
        const url = URL.createObjectURL(files[0])
        this.$refs.imgRef.setAttribute('src', url)
      }

      // Â∞ÅÈù¢ÂõæÁâáÂçïÁã¨Ê†°È™åËß£ÂÜ≥
      this.$refs.pubFormRef.validateField('cover_img')
    },
    // ÂèëÂ∏ÉÊñáÁ´†ÊàñËçâÁ®ø-ÊåâÈíÆÁÇπÂáª‰∫ã‰ª∂
    pubArticleFn(str) {
      // 1. ËÆæÁΩÆÂèëÂ∏ÉÁä∂ÊÄÅ
      this.pubForm.state = str

      // // 2. Ë°®ÂçïÈ¢ÑÊ†°È™åÔºàÂÖúÂ∫ïÊ†°È™åÔºâ
      this.$refs.pubFormRef.validate(async (valid) => {
        if (valid) {
          // ÂàõÂª∫ FormData Ë°®ÂçïÊï∞ÊçÆÂØπË±°
          // FormDataÁ±ªÊòØTHML5ÁöÑÂÜÖÂÆπÔºàÊòØ‰∏Ä‰∏™Áî®‰∫éË£ÖÊñá‰ª∂ÁöÑÂÆπÂô®Ôºâ
          const fd = new FormData()

          // Âêë FormData ‰∏≠ËøΩÂä†Êï∞ÊçÆ
          // ÊñπÂºè1
          // fd.append('title', this.pubForm.title)
          // fd.append('cate_id', this.pubForm.cate_id)
          // fd.append('content', this.pubForm.content)
          // fd.append('cover_img', this.pubForm.cover_img)
          // fd.append('state', this.pubForm.state)
          // ÊñπÂºè2Ôºö‰ΩøÁî®Object.keysÂä†forEach()
          Object.keys(this.pubForm).forEach((key) => {
            fd.append(key, this.pubForm[key])
          })
          console.log(fd)
          // ÂèëËµ∑ËØ∑Ê±Ç
          const { data: res } = await uploadArticleAPI(fd)
          console.log(res)
          if (res.code !== 0) {
            this.$message.error('ÂèëÂ∏ÉÊñáÁ´†Â§±Ë¥•ÔºÅ')
          } else {
            this.$message.success('ÂèëÂ∏ÉÊñáÁ´†ÊàêÂäüÔºÅ')

            // ÂÖ≥Èó≠ÂØπËØùÊ°Ü
            this.pubdialogVisible = false

            // Âà∑Êñ∞‰∏ªÈ°µÈù¢ÊñáÁ´†ÂàóË°®Êï∞ÊçÆ
            this.getArtListFn()
          }
        } else {
          return false
        }
      })
    },
    // ‰ΩøÁî®element-ui‰∏≠ÁöÑvalidateField()ÊñπÊ≥ïËÆ©ÂØåÊñáÊú¨ÁºñËæëÂô®ÂÆûÁé∞Ê†°È™åÂäüËÉΩ
    contentChangeFn() {
      // validateField()ÂØπÈÉ®ÂàÜË°®ÂçïÂ≠óÊÆµËøõË°åÊ†°È™åÁöÑÊñπÊ≥ï
      this.$refs.pubFormRef.validateField('content')
    },
    // ÂèëÂ∏ÉÊñáÁ´†ÊàñËçâÁ®øÂêéÁöÑÊìç‰Ωú
    dialogCloseFn() {
      // Ê∏ÖÁ©∫ÂÖ≥ÈîÆÊï∞ÊçÆ
      this.$refs.pubFormRef.resetFields()
      // Âõ†‰∏∫Ëøô2‰∏™ÂèòÈáèÂØπÂ∫îÁöÑÊ†áÁ≠æ‰∏çÊòØË°®ÂçïÁªëÂÆöÁöÑ, ÊâÄ‰ª•ÈúÄË¶Å‚ú®ÂçïÁã¨ÊéßÂà∂
      this.pubForm.content = ''
      this.$refs.imgRef.setAttribute('src', defaultImg)
    },
    // ÊñáÁ´†ÂàóË°®Êù°Êï∞ÂèëÁîüÊîπÂèòÊó∂(element-uiÂÜÖÁΩÆÂõûË∞ÉÂèÇÊï∞)
    handleSizeChangeFn(newSize) {
      // ‰∏∫ pagesize ËµãÂÄº
      this.q.pagesize = newSize

      // Ëß£ÂÜ≥Âú®ÈùûÁ¨¨‰∏ÄÈ°µÊñáÁ´†ÂàóË°®ËøõË°åÂàóË°®Êù°Êï∞nÂàáÊç¢ÂêéÔºåÂÜÖÂÆπ‰∏çÊòæÁ§∫ÁöÑbug
      // ÈóÆÈ¢òÂàÜÊûê1ÔºöÈÄöËøánetworkÂèØ‰ª•ÂèëÁé∞ÂàáÊç¢ÂàÜÈ°µÊó∂ÔºåÂèëÁîü‰∫Ü‰∏§Ê¨°getArtListFn()ËØ∑Ê±Ç
      // bug‰∫ßÁîüÂéüÂõ†ÔºöÂØπÊØîËØ∑Ê±ÇÂèØ‰ª•ÂèëÁé∞ ‚Äî‚Äî
      // ‚ë† Áî±‰∫éÊù°Êï∞ÊîπÂèòËÄåËß¶ÂèëgetArtListFn()ËØ∑Ê±Ç„ÄêÂΩìÁÑ∂È°µÁöÑnÊù°Êï∞ÊçÆ„ÄëÔºåÁªìÊûú‰∏∫Á©∫
      // ‚ë° Áî±‰∫éÈ°µÈù¢ÊîπÂèòËÄåËß¶ÂèëgetArtListFn()ËØ∑Ê±Ç„ÄêÈ°µÈù¢ÊîπÂèòÂêéÁöÑnÊù°Êï∞ÊçÆ„ÄëÔºåÁªìÊûúÊ≠£Â∏∏
      // Ëã•‚ë†ËØ∑Ê±ÇÁöÑÈÄüÂ∫¶Âø´‰∏é‚ë°ËØ∑Ê±ÇÁöÑÈÄüÂ∫¶Êó∂ÔºåÂ∞±‰ºö‰∫ßÁîü‰∏Ä‰∏™‚ú®ÂÅ∂ÂèëÊÄßÁöÑbug‚ú®
      // Ëß£ÂÜ≥ÈóÆÈ¢òÔºö
      // ÈªòËÆ§Â±ïÁ§∫Á¨¨‰∏ÄÈ°µÊï∞ÊçÆÔºàÊúâËÉΩÂäõÁöÑ‰πüÂèØ‰ª•ËÆ©‰∫∫Â±ïÁ§∫ÊúÄÂêé‰∏ÄÈ°µÊï∞ÊçÆÔºâ
      this.q.pagenum = 1

      // ÈáçÊñ∞ÂèëËµ∑ËØ∑Ê±Ç
      this.getArtListFn()
    },
    // ÊñáÁ´†ÂàóË°®È°µÊï∞ÂèëÁîüÊîπÂèòÊó∂(element-uiÂÜÖÁΩÆÂõûË∞ÉÂèÇÊï∞)
    handleCurrentChangeFn(newPage) {
      // ‰∏∫È°µÁ†ÅÂÄºËµãÂÄº
      this.q.pagenum = newPage
      // ÈáçÊñ∞ÂèëËµ∑ËØ∑Ê±Ç
      this.getArtListFn()
    },
    // Ëß¶ÂèëÁ≠õÈÄâÊñáÁ´†ÊåâÈíÆ
    onChooseFn() {
      this.getArtListFn()
    },
    // Ëß¶ÂèëÈáçÁΩÆÊñáÁ´†Á≠õÈÄâÊåâÈíÆ
    resetChooseFn() {
      // 1. ÈáçÁΩÆÊü•ËØ¢ÂèÇÊï∞ÂØπË±°
      this.q = {
        pagenum: 1,
        pagesize: 5,
        cate_id: '',
        state: ''
      }
      // 2. ÈáçÊñ∞ÂèëËµ∑ËØ∑Ê±Ç
      this.getArtListFn()
    },
    // Â±ïÁ§∫ÊñáÁ´†ËØ¶ÊÉÖÈìæÊé•
    async showDetailFn(id) {
      const { data: res } = await getArticleDetailFn(id)
      // console.log(res)

      if (res.code !== 0) return this.$message.error('Ëé∑ÂèñÊñáÁ´†ËØ¶ÊÉÖÂ§±Ë¥•!')
      this.artDetail = res.data
      // Â±ïÁ§∫ÊñáÁ´†ËØ¶ÊÉÖÂØπËØùÊ°Ü
      this.detailVisible = true
    },
    // ÊñáÁ´†Âà†Èô§ÊåâÈíÆ
    async removeArticleFn(id) {
      // 1. ËØ¢ÈóÆÁî®Êà∑ÊòØÂê¶Ë¶ÅÂà†Èô§
      const confirmResult = await this.$confirm(
        'Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§ËØ•Êñá‰ª∂, ÊòØÂê¶ÁªßÁª≠?',
        'ÊèêÁ§∫',
        {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }
      ).catch((err) => err)

      // 2. ÂèñÊ∂à‰∫ÜÂà†Èô§
      if (confirmResult === 'cancel') return

      // ÊâßË°åÂà†Èô§ÁöÑÊìç‰Ωú
      const { data: res } = await delArticleAPI(id)

      if (res.code !== 0) return this.$message.error('Âà†Èô§ÊñáÁ´†Â§±Ë¥•!')
      this.$message.success('Âà†Èô§ÊñáÁ´†ÊàêÂäü!')

      // ÈóÆÈ¢òÔºöÂú®ÊúÄÂêé‰∏ÄÈ°µ‰∏≠Âà†Èô§ÊúÄÂêé‰∏ÄÁØáÊñáÁ´†Êó∂ÔºåËôΩÁÑ∂È°µÁ†ÅËÉΩ‰ºöÂà∞‰∏ä‰∏ÄÈ°µÔºå‰ΩÜÊï∞ÊçÆÊ≤°ÊúâÂá∫Áé∞ÁöÑÂ∞èbug
      // Ëß£ÂÜ≥ÊñπÊ≥ïÔºöËÆ©ÂÆÉÈáçÁÇπË∑≥ËΩ¨Âà∞‰∏ä‰∏ÄÈ°µ
      // if (this.artList.length === 1) {
      //   if (this.q.pagenum > 1) {
      //     this.q.pagenum--
      //   }
      // }

      // Âà∑Êñ∞ÂàóË°®Êï∞ÊçÆ
      this.getArtListFn()
    }
  }
}
</script>
